body {
    background-color: lightblue;
}

.container {
    display: flex; 
    justify-content: space-between; /* Align items horizontally */
    align-items: center; /* Align items vertically */
    height: 95vh; /* Make the container take up the full screen height */
}

.left {
    flex: 1;
    display: flex;
    align-items: center;
    padding: 1rem;
}

.middle {
    flex: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    margin: 0 3px;
}

.right {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.drawing-board {
    border: 5px solid black;
    background-color: white;
}

.chat-container {
    display: flex;
    flex-direction: column;
    height: 600px;
}

.chat-log {
    flex: 1;  /* Make chat log fill the remaining space */
    overflow-y: auto;
    word-break: break-word;
    background-color: white;
    border-radius: 5px;
    padding: 1rem;
    margin-bottom: 1rem;
}

.chat-box {
    flex-shrink: 0;
}

.chat-box input {
    width: 100%;
    padding: 0.5rem;
    border-radius: 5px;
}

.guess {
    border: 1px solid black;
    padding: 5px;
    background-color: lightgray;
    width: 250px;
    word-break: break-word;
}

.players-list {
    font-size: 24px;
}

.drawing-board-container {
    text-align: center;
}

.link {
    padding: 5px;
    text-decoration: none;
    font-size: 24px;
    color: inherit;
}

.link:hover {
    background-color: #75C2F6;
}

ul {
    padding: 0;
    list-style-type: none;
}
